.select-container {
  margin: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  background-color: var(--background);
  color: var(--text);
  border-radius: 5px;
  padding: 10px;
}

.select-container .react-select__control {
  background-color: var(--background);
  color: var(--text);
}

.select-container .react-select__menu {
  background-color: var(--background);
  color: var(--text);
}
